import Vue from 'vue'
import Router from 'vue-router'
import Posts from '@/components/Posts'
import Admin from '@/components/Admin'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/admin',
      name: 'Admin',
      component: Admin,
      children: [
        {
          path: 'posts',
          name: 'Posts',
          component: Posts
        }
      ]
    }
  ]
})



