<template lang="html">
  <div class="posts">
    <ButtonGroup>
      <Button @click="edit">新建</Button>
      <Button>删除</Button>
    </ButtonGroup>
    <Table :columns="cols" :data="titleArr"></Table>
    <Modal v-model="editVisible" width="90%" title="新建" :closable="false"
    :mask-closable="false" @on-ok="save">
      <Row>
        <Input v-model="editData.title"></Input>
      </Row>
      <Row>
        <Col :span="12">
          <Input type="textarea" :rows="30" v-model="editData.content"></Input>
        </Col>
        <Col :span="12">
          <Card><div v-html="markedHtml"></div></Card>
        </Col>
      </Row>
    </Modal>
  </div>
</template>

<script>
import marked from 'marked'
export default {
  data () {
    return {
      titleArr: [],
      editData: {
        title: '',
        content: ''
      },
      editVisible: false,
      cols: [
        {
          type: 'selection'
        }, {
          title: '标题',
          key: 'title'
        }, {
          title: '操作',
          render: (h, params) => {
            return (
              <button-group>
                <i-button type="primary"
                onClick={ () => { this.edit(params.row) } } size="small">编辑</i-button>
                <i-button type="error" size="small">删除</i-button>
              </button-group>
            )
          }
        }
      ],
      postList: []
    }
  },
  methods: {
    edit (data) {
      if (data) {
        axios.get(`/api/posts/${data.id}`).then(res => {
          if (res.status === 200) {
            this.editData = res.data
          }
        })
      }
      this.editVisible = true
    },
    save () {
      axios.post('/api/posts', this.editData).then(res => {
        if (res.status === 200) {
          this.fetchPosts()
        } else {
          this.$Message.error(res.msg)
        }
      })
    },
    fetchPosts () {
      axios.get('/api/posts').then(res => {
        this.titleArr = res.data
      })
    }
  },
  computed: {
    markedHtml () {
      return marked(this.editData.content)
    }
  },
  created () {
    this.fetchPosts()
  }
}
</script>

<style lang="css">
</style>



